import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import VerticalTabs from './VerticalTab';
import Container from '@material-ui/core/Container';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  hero: {
    backgroundImage: ` url("https://b.zmtcdn.com/data/pictures/chains/7/16581307/26c9d5533a7411c8cfbab8820d89c454.jpg")`,
    height: "350px",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    position: "relative",
    [theme.breakpoints.down("sm")]: {
      height: 300,
    }
  },
  root: {
    flexGrow: 2,
    width: '100%',
    backgroundColor: theme.palette.background.white,
  },
  tabitem:{
    borderBottom: `2px solid ${theme.palette.divider}`,
    fontWeight : "bold",
  }
}));

export default function Tabfinal() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div>
       <Box className={classes.hero}>  
      </Box>
  <Container maxWidth="md">
  <Typography component="h1" variant="h4" align="left" color="white"  gutterBottom>
    <h4 style={{ color:"#311b92",fontFamily:"Playfair Display"}} >Kritunga Restaurant</h4>
        </Typography>
        <Typography  fontsize="20px">
      Lebance,Bakery,Fastfood<br/>
      Charminar<br/>
Open now
12noon – 12midnight (Today)
 <br/><br/>
        </Typography>
        <br/>
    <div className={classes.root}>
     
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
          className={classes.tabitem}
        >
          <Tab label="Overview" {...a11yProps(0)} />
          <Tab label="Order Online" {...a11yProps(1)} />
          <Tab label="Menu" {...a11yProps(2)} />
          <Tab label="Review" {...a11yProps(3)} />
          <Tab label="Photos" {...a11yProps(4)} />

        
        </Tabs>
      <TabPanel value={value} index={0}>
        Overview
      </TabPanel>
      <TabPanel value={value} index={1}>
      <VerticalTabs/>
      </TabPanel>
      <TabPanel value={value} index={2}>
        Menu
      </TabPanel>
      <TabPanel value={value} index={3}>
        Review
      </TabPanel>
      <TabPanel value={value} index={4}>
        Photos
      </TabPanel>
    </div> </Container></div>
  );
}
